.scr_review_modal__wrapper {
  img {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 4;
    width: auto;
    height: auto;
    max-height: 95%;
    max-width: 95%;
    display: block;
    object-fit: contain;
  }
}

.scr_review_modal__btn {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 5;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  // background-color: rgba(0, 0, 0, 0.3);
  background-color: rgba(28, 28, 32, 0.3);

  path {
    fill: #fff;
    transition: all 0.2s ease-out;
  }

  // @media (hover: hover) {
